.Input {
    margin-bottom: 15px;
}
.Input label {
    color: #20232a;
    margin-bottom: 3px;
    padding: 0;
    display: block;
    font-weight: bold;
}
.Input input {
    box-sizing: border-box;
    display: block;
    border: 1px solid #20232a;
    padding: 7px;
    margin: 0 0 5px;
    width: 100%;
    outline: none;
    transition: all 300ms ease-in-out;
}
.Input span {
    color: #f01f30;
    font-size: 12px;
    font-weight: bold;
}
.Input.invalid label{
    color: #f01f30;
}
