.Auth {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
}

.Auth > div {
    width: 100%;
    max-width: 600px;
    padding: 0 20px;
}

.Auth h1 {
    text-align: center;
}

.AuthForm {
    background: #eee;
    box-shadow: 2px 2px 2px rgba(255, 255 ,255, .7);
    padding: 15px;
    border-radius: 5px;
}