.Button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #000;
    color: #20232a;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
}
.Button:focus {
    outline: none;
}
.Button:active {
    box-shadow: inset 2px 2px 1px rgba(0, 0, 0, .35);
}
.Button:disabled {
    background: #ccc;
    color: #000;
    cursor: not-allowed;
}
.error {
    background: rgba(240,87,108, .7);;
}
.succes {
    background: rgba(161, 240, 69, .7);;
}
.primary {
    background: rgb(97, 218, 251);
}