.AnswerItem {
    border: 1px solid #ccc;
    padding: 5px 10px;
    border-radius: 5px;
    margin-bottom: 7px;
    cursor: pointer;
}

.AnswerItem:hover {
    background: rgba(255,255,255, .1);
    transition: background .3s ease-in-out;
}
.AnswerItem.succes {
    background: rgba(161,240,69, .7);
    color: #fff;
}
.AnswerItem.error {
    background: rgba(240,87,108, .7);
    color: #fff;
}