.QuizCreator {
    display: flex;
    justify-content: center;
    flex-grow: 1;
    width: 100%;
    padding-top: 100px;

}
.QuizCreator > div {
    width: 100%;
    max-width:600px;
    padding: 0 20px;

}
.QuizCreator h1 {
    text-align: center;
}
.QuizCreator form {
    padding: 15px;
    border-radius: 5px;
    box-shadow: 2px 2px 2px rgba(255, 255, 255, .7);
    background: #eee;
}