.QuizList {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
}
.QuizList ul {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: center;
}
.QuizList li {
    font-size: 18px;
    margin-bottom: 15px;
}
.QuizList li a {
    color: #fff;
    text-decoration: none;
}
.QuizList li a:hover {
    color: #61DAFB
}